.HistoryCell {
    width: 80%;
    border: 1px solid #eee;
    -webkit-box-shadow: 0 2px 3px #ccc;
            box-shadow: 0 2px 3px #ccc;
    padding: 10px;
    margin: 10px auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: baseline;
        align-items: baseline;
}

.WorkedTime {
    -ms-flex-positive: 10;
        flex-grow: 10;
}

.WorkedDate > p,
.WorkedTime > p,
.Earned > p
 {
    margin: 0;
}

.WorkedDate,
.WorkedTime,
.Earned {
    margin: 0;
}

.WorkedDate {
    padding: 0 20px;
    /* border: 1px solid #eee */
}

.WorkedTime,
.Earned {
    margin-top: 20px;
}

.Day {
    font-size: 3em;
    color: #525252;
}

.Month,
.Year,
.WorkedPeriods {
    color: #ccc;
}

.Day,
.Month,
.Year {
    text-align: center;
}

.WorkedSum,
.EarnedSum {
    font-size: 2em;
}

.WorkedSum,
.EarnedSum,
.WorkedPeriods,
.Billed
{
    padding: 0 10px;
}

.Billed {
    text-align: center;
}

.BilledColor {
    color: green;
    text-align: center;

}

.NotBilledColor {
    color: red;
    text-align: center;

}

@media (max-width: 450px) {
    .WorkedSum,
    .EarnedSum,
    .WorkedPeriods,
    .Billed
    {
        padding: 0 5px;
    }
}

@media (max-width: 500px) {
    .HistoryCell {
        width: 99%;
    }

    .WorkedSum,
    .EarnedSum {
        font-size: 1.5em;
    }

    .WorkedTime,
    .Earned {
        margin-top: 5px;
    }

    .Day {
        font-size: 2em;
    }
}




